import Style from "ol/style/Style";
import Stroke from "ol/style/Stroke";
import Fill from "ol/style/Fill";
import CircleStyle from "ol/style/Circle"
import { FeatureLike } from "ol/Feature";

export const activeToggleColor: string = 'rgba(141,174,16,1)';
export const strokeOpacity: number = 0.75;
export const fillOpacity: number = 0.25;
export const selectedColor: string[] = [
    `rgba(141,174, 16,${strokeOpacity})`,
    `rgba(141,174, 16,${fillOpacity})`,
];
export const defaultColor: string[] = [
    `rgba(11, 74, 119,${strokeOpacity})`,
    `rgba(11, 74, 119,${fillOpacity})`,
];
export const trackColor: string[] = [
    `rgba(168,0,2,${strokeOpacity})`,
    `rgba(50,188,0,${strokeOpacity})`
]

export const insertedStyle: Style = new Style({
    stroke: new Stroke({
        color: trackColor[1],
        width: 2,
    }),
});


//fillColor koelsch
function styleFillColor(feature: FeatureLike): Object {
    let sorte: string = feature.get('name')
    switch (sorte) {
      case 'Bussard': return { color: '#ddd45f' };
      case 'Turmfalke': return { color: '#0d5123' };      
      default: return { color: '#474749' };
    }
  }
  
  //strokeColor koelsch
  function styleStrokeColor(feature: FeatureLike): Object {
    let sorte: string = feature.get('name')
    switch (sorte) {
      case 'Bussard': return { color: '#ff9d3d', width: 2 };
      case 'Turmfalke': return { color: '#888ddd', width: 2 };      
      default: return { color: '#d3d3d3', width: 2 };
    }
  }

export function styleBird(feature: FeatureLike): Style {
    let birdStyle: Style = new Style({
      image: new CircleStyle({
        radius: 6,
        fill: new Fill(styleFillColor(feature)),
        stroke: new Stroke(styleStrokeColor(feature))
      })
    })
    return birdStyle;
  }
export const defaultPoly = new Style({
    stroke: new Stroke({
        // color: feature.get('selected') ? selectedColor[0] : defaultColor[0],
        color: defaultColor[0],
        width: 1
    }),
    fill: new Fill({
        color: defaultColor[1]
        // color: feature.get('selected') ? selectedColor[1] : defaultColor[1],
    })
});


export const selectedStyle = new Style({
    stroke: new Stroke({
        color: selectedColor[0],
        width: 3,
    }),
    fill: new Fill({
        color: selectedColor[1],
    })
});

export const lineStyle = new Style({
    fill: new Fill({
        color: 'rgba(255, 25, 255, 0.2)'
    }),
    stroke: new Stroke({
        color: 'rgba(100, 0, 50, 0.5)',
        width: 4
    }),
    image: new CircleStyle({
        radius: 5,
        stroke: new Stroke({
            color: 'rgba(0, 0, 0, 0.7)'
        }),
        fill: new Fill({
            color: 'rgba(255, 255, 255, 0.7)'
        })
    })
})

export function defaultStyle(feature: FeatureLike): Style {
    //@ts-ignore
    let featType = feature!.getGeometry().getType()
    if (featType === "MultiPolygon") {
        let style: Style = defaultPoly
        return style;
    }
    else if (featType === "LineString") {
        return lineStyle
    }
    else {
        return styleBird(feature)
    }
}

