
import React, { useState, useEffect } from 'react';
import 'ol/ol.css';
import { Map } from 'ol'
import { IMapProps, IMapState } from '../types/types';
import { MapUtils } from '../utils/MapUtils';
import MapSidebar from './MapSidebar';
import data from '../data/friedhoefe_3857.json';

const Basemap = (props: IMapProps) => {
    //@ts-ignore
    const [map, setMap] = useState<any | IMapState>(null);

    useEffect(() => {
        const basemap: Map = MapUtils.createMap();
        setMap(basemap);        
        const gyLayer = MapUtils.createVector("Friedhof", data);       
        basemap.addLayer(gyLayer);        
        const birds = MapUtils.createVectorGroup("Greifvgel");
        basemap.addLayer(birds);
        // const select = MapUtils.createSelect();
        // basemap.addInteraction(select);
        MapUtils.createBar(basemap)
    }, []);

    return (
        <>
            <div id='map' className='map'></div>
            <MapSidebar map={map} />
        </>
    )
}

export default Basemap;