import Style from "ol/style/Style"
import { Feature } from "ol"
import Point from "ol/geom/Point"
import Fill from "ol/style/Fill"
import Stroke from "ol/style/Stroke"
import CircleStyle from "ol/style/Circle"
import Polygon from "ol/geom/Polygon"
import { defaultPoly } from "./MapStyles"

export class StyleCreator {
    item: string
    constructor(item: any) {
        this.item = item
    }

    getColor(): string {
        const randomColor = Math.floor(Math.random() * 16777215).toString(16)
        return '#' + randomColor
    }
}

export class LegendPoint {
    pointFeat: any;
    constructor(style: Style) {
        this.pointFeat = new Feature(new Point([0, 0]))
        this.pointFeat.setStyle(style)
    }

    getFeature() {
        return this.pointFeat
    }
}

export class LegendPoly {
    polyFeat: any;
    constructor(style: Style) {
        this.polyFeat = new Feature(new Polygon([[[0, 0], [1, 1], [2, 2]]]))
        this.polyFeat.setStyle(style)
    }

    getFeature() {
        return this.polyFeat
    }
}

 

const labels: string[] = ["Bussard", "Turmfalke"];
const labelColors: string[] = ["#ddd45f", "#0d5123"];
const labelStrokeColors: string[] = ["#ff9d3d", "#888ddd"];
const legendObj: Object = {}

// for (let item of labels) {
//     let color = new StyleCreator(item).getColor()
//     // labelColors.push(color)
// }
//@ts-ignore
labels.forEach((label: string, index: number) => legendObj[label] = labelColors[index]);
let legendList: string[][] = []

Object.entries(legendObj).forEach((item, i) => {
    const pointDefault = new Style({
        //@ts-ignore
        image: new CircleStyle({
            radius: 7,
            //@ts-ignore
            fill: new Fill({ color: item[1] }),
            stroke: new Stroke({
                color: labelStrokeColors[i],
                width: 2,
            })
        })
    });
    const point = new LegendPoint(pointDefault).getFeature()
    legendList.push([item[0], point]);
})

const polyFeat = new LegendPoly(defaultPoly).getFeature()
export function fillLegend(legend: any) {
    for (let i of legendList) {
        legend.addItem({ title: i[0], feature: i[1], typeGeom: 'Point', className: 'svg', })
    }
    legend.addItem({ title: "Friedhof", feature: polyFeat, typeGeom: 'Polygon', className: 'poly', })

}


