import WFS from 'ol/format/WFS';
import View from "ol/View";
import TileLayer from 'ol/layer/Tile';
import {Stamen, OSM} from 'ol/source';
import LayerGroup from 'ol/layer/Group';


enum BaseLayers {
    Osm = 'http://tile.openstreetmap.org/{z}/{x}/{y}.png',
    OsmBw = 'https://tiles.wmflabs.org/bw-mapnik/${z}/${x}/${y}.png',
    CartoDark = 'https://{1-4}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}{r}.png',
    CartoLight = 'https://{1-4}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}{r}.png',
    CartoVoyager = 'https://{1-4}.basemaps.cartocdn.com/rastertiles/voyager/{z}/{x}/{y}{r}.png',
    EsriLight = 'https://server.arcgisonline.com/ArcGIS/rest/services/Canvas/World_Light_Gray_Base/MapServer/tile/{z}/{y}/{x}',
    EsriDark = 'https://server.arcgisonline.com/ArcGIS/rest/services/Canvas/World_Dark_Gray_Base/MapServer/tile/{z}/{y}/{x}',
    EsriImg = 'https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}'
}

export const baseLayerUrl: string = BaseLayers.CartoVoyager;
export const cartoLight: string =  BaseLayers.CartoLight;
export const esriImg: string = BaseLayers.EsriImg;
export const mapCenter: number[] = [774577.0148,6610935.8641];
export const mapZoom: number = 11;
export const maxZoom: number = 19;
export const minZoom: number = 10;
export const rotationStatus: boolean = false;
export const view = new View({
    center: mapCenter,
    zoom: mapZoom,
    maxZoom: maxZoom,
    minZoom: minZoom,
    enableRotation: rotationStatus,
})

export const wfsTransaction: WFS = new WFS();

export enum Icons {
    Home = "fa fa-home",
    Info = "fa fa-info",
    Twitter = "fa fa-twitter",
    Cogs = "fa fa-cogs",
    Tools = "fa fa-wrench",
    Map = "fa fa-map",
    Data = "fa fa-database",
    Down = "fa fa-chevron-down",
    Up = "fa fa-chevron-up",
    Trash = "fa fa-trash",
    Csv = "fa fa-file-excel-o",
    Pdf = "fa fa-file-pdf-o",
    Path = "fa fa-arrows-h",
    Circle = "fa fa-circle",
    Square = "fa fa-square-o",
    Add = "fa fa-plus",
    Del = "fa fa-minus",
    Png = "fa fa-file-image-o",
  }

  export const baseLayers = new LayerGroup({
    //@ts-ignore
    title: 'Base Layers',
    openInLayerSwitcher: true,
    layers: [
      new TileLayer({
          //@ts-ignore
        title: "Watercolor",
        baseLayer: true,
        source: new Stamen({ layer: 'watercolor' })
      }),
      new TileLayer({
        //@ts-ignore
        title: "Toner",
        baseLayer: true,
        visible: false,
        source: new Stamen({ layer: 'toner' })
      }),
      new TileLayer({
          //@ts-ignore
        title: "OSM",
        baseLayer: true,
        source: new OSM(),
        visible: false
      })
    ]
  });



