
import { Map } from 'ol';
import TileLayer from 'ol/layer/Tile';
import VectorSource from 'ol/source/Vector';
import GeoJSON from 'ol/format/GeoJSON';
import { shiftKeyOnly } from 'ol/events/condition';
import VectorLayer from 'ol/layer/Vector';
import Select from 'ol/interaction/Select';
import XYZSource from 'ol/source/XYZ';
import { baseLayerUrl, view, cartoLight, esriImg } from './MapConfig';
import { selectedStyle, defaultStyle } from './MapStyles';
import Draw from 'ol/interaction/Draw';
import GeometryType from 'ol/geom/GeometryType';
import { ScaleLine, defaults as defaultControls } from 'ol/control';
import * as Bar from 'ol-ext/control/Bar';
import * as Button from 'ol-ext/control/Button';
import * as Toggle from 'ol-ext/control/Toggle';
import * as TextButton from 'ol-ext/control/TextButton';
import * as Legend from 'ol-ext/legend/Legend';
import * as LegendControl from 'ol-ext/control/Legend';
import LineString from 'ol/geom/LineString';
import Polygon from 'ol/geom/Polygon';
import { fillLegend } from './LegendConfig';
import * as Tooltip from 'ol-ext/overlay/Tooltip'
import * as LayerSwitcher from 'ol-ext/control/LayerSwitcher'
import { OSM } from 'ol/source';
import LayerGroup from 'ol/layer/Group';
import { saveAs } from 'file-saver';
import bussard from '../data/bussard.json';
import turmfalke from '../data/turmfalke.json';

export class MapUtils {

    static createBaseGroup() {
        const baseLayers = new LayerGroup({
            //@ts-ignore
            title: 'BaseLayers',
            openInLayerSwitcher: true,
            layers: [
                new TileLayer({
                    //@ts-ignore
                    title: "OSM",
                    baseLayer: true,
                    source: new OSM(),
                    visible: false
                }),
                this.createBaseLayer(baseLayerUrl, "CartoDB Voyager", true),
                this.createBaseLayer(cartoLight, "CartoDB Light", false),
                this.createBaseLayer(esriImg, "AerialImagery", false)
            ]
        });
        return baseLayers
    }

    static createBaseLayer(xyzUrl: string, title: string, visible: boolean) {
        const xyzURL: string = xyzUrl;

        const baseSource: XYZSource = new XYZSource({
            crossOrigin: 'anonymous',
            url: xyzURL
        });
        //@ts-ignore
        const baseLayer: TileLayer = new TileLayer({
            //@ts-ignore
            title: title,
            baseLayer: true,
            source: baseSource,
            visible: visible
        });

        return baseLayer;
    }

    static createMap() {
        const baseGroup: LayerGroup = this.createBaseGroup();
        //@ts-ignore
        const map: Map = new Map({
            target: 'map',
            layers: [baseGroup],
            view: view,
            controls: defaultControls().extend([this.createScale()]),
        });
        return map;
    }
    //creates a vector source based on a publised geoserver layer
    static createSource(data: Object) {
        const source: VectorSource<any> = new VectorSource({
            features: new GeoJSON().readFeatures(data, {
                dataProjection: 'EPSG:3857',
                featureProjection: 'EPSG:3857'
            })
        });
        return source;
    }
    //creates a vector layer based on a vector source from a published geoserver layer
    static createVector(title: string, data: Object) {
        //@ts-ignore
        const vectorSource: VectorSource<any> = this.createSource(data);
        //@ts-ignore
        const vectorLayer: VectorLayer = new VectorLayer({
            //@ts-ignore
            title: title,
            source: vectorSource,
            style: defaultStyle,
        });
        // let geomType = vectorLayer.getSource().getFeatures()[0].getGeometry().getType();
        // geomType === "Polygon" ? vectorLayer.setStyle(defaultStyle) : vectorLayer.setStyle(defaultStyle);
        // vectorLayer.setMinZoom(16)
        return vectorLayer;
    }

    static createVectorGroup(title: string) {
        const vectorGroup = new LayerGroup({
            //@ts-ignore
            title: title,
            openInLayerSwitcher: true,
            layers: [
                this.createVector("Bussard", bussard),
                this.createVector("Turmfalke", turmfalke),
            ]
        });
        return vectorGroup
    }
    //creates select interaction
    static createSelect(): Select {
        const select: Select = new Select({
            toggleCondition: shiftKeyOnly,
            style: selectedStyle
        });
        return select;
    }

    static createScale() {
        let control = new ScaleLine({
            className: 'my-scale-line',
        })
        return control
    }

    static createBar(map: Map) {
        // Main control bar
        //@ts-ignore
        let vector: VectorLayer = new VectorLayer({
            //@ts-ignore
            displayInLayerSwitcher: false,
            source: new VectorSource()
        });

        let mainbar = new Bar.default({
            className: 'ol-control.ol-bar.ol-top.ol-right'
        });
        map.addLayer(vector)
        mainbar.setPosition('right')
        // Edit control bar 
        var editbar = new Bar.default({
            className: 'myEditBar',
            toggleOne: true,	// one control active at the same time
            group: true			// group controls together
        });

        let save = new Button.default({
            html: '<i class="fa fa-download"></i>',
            title: "Export drawn Features",
            handleClick: function (e) {
                let json = new GeoJSON().writeFeatures(vector.getSource().getFeatures());
                let featArr = [json];
                let blob = new Blob(featArr, { type: 'text/json;charset=utf8' });
                saveAs(blob, 'drawnFeatures.geojson')
            }
        });

        // Add editing tools
        let pedit = new Toggle.default({
            html: '<i class="fa fa-map-marker" ></i>',
            title: 'Draw Point Feature',
            //@ts-ignore
            interaction: new Draw({
                type: GeometryType.POINT,
                source: vector.getSource()
            })
        });
        var sbar = new Bar.default({
            className: 'mySelectBar',
        });
        sbar.addControl(new Button.default({
            html: '<i class="fa fa-times"></i>',
            title: "Delete Feature",
            handleClick: function () {
                let layersArr = map.getLayers().getArray();
                let vecLayers = layersArr.filter((v, i) => i !== 0 && i !== layersArr.length - 1);
                let featArr = [];
                let vecFeatures;
                for (let layer of vecLayers) {
                    let vec = layer as VectorLayer<any>;
                    // let vec = layer as VectorLayer<any>;
                    try {
                        let vecSource = vec.getSource();
                        vecFeatures = vecSource.getFeatures();
                        featArr.push(vecFeatures);
                    }
                    catch {
                        let groupLayers = vec.getLayersArray();
                        for (let groupLayer of groupLayers) {
                            let groupVec = groupLayer as VectorLayer<any>;
                            let vecSource = groupVec.getSource();
                            vecFeatures = vecSource.getFeatures();
                            featArr.push(vecFeatures);
                        }
                    }
                }
                let excludedFeats: any[] = [].concat.apply([], featArr);
                //@ts-ignore
                let features = selectCtrl.getInteraction().getFeatures();
                //@ts-ignore
                if (!features.getLength()) tooltip.setInfo("Select an drawn object first...");
                else {
                    for (var i = 0, f; f = features.item(i); i++) {
                        if (!excludedFeats.includes(f)) {
                            vector.getSource().removeFeature(f);
                        }
                        else {
                            tooltip.setInfo("Feature cannot be deleted. Only drawn features can be deleted.")
                        }
                    }
                    //@ts-ignore
                    selectCtrl.getInteraction().getFeatures().clear();
                }
            }
        }));

        sbar.addControl(new Button.default({
            html: '<i class="fa fa-info"></i>',
            title: "Get Feature Info",
            handleClick: function () {
                //@ts-ignore
                switch (selectCtrl.getInteraction().getFeatures().getLength()) {
                    case 0: console.log("Select an object first...");
                        break;
                    case 1:
                        //@ts-ignore
                        let f = selectCtrl.getInteraction().getFeatures().item(0);
                        let propKeys = Object.keys(f.getProperties());
                        propKeys.map((t, k) => { t !== 'selected' && t !== 'geometry' ? tooltip.setInfo(`${t}: ` + f.get(t)) : tooltip.setInfo('') });
                        break;
                    default:
                        //@ts-ignore
                        console.log(selectCtrl.getInteraction().getFeatures().getLength() + " objects selected.");
                        break;
                }
            }
        }));
        let selectCtrl = new Toggle.default({
            html: '<i class="fa fa-hand-pointer-o"></i>',
            title: "Select Feature",
            //@ts-ignore
            interaction: new Select({ hitTolerance: 2 }),
            bar: sbar,
            autoActive: true,
            active: false
        });
        var ledit = new Toggle.default({
            html: '<i class="fa fa-share-alt" ></i>',
            title: 'Draw LineString Feature',
            //@ts-ignore
            interaction: new Draw({
                type: GeometryType.LINE_STRING,
                source: vector.getSource(),
                // Count inserted points
                geometryFunction: function (coordinates, geometry) {
                    if (geometry) geometry.setCoordinates(coordinates);
                    //@ts-ignore
                    else geometry = new LineString(coordinates);
                    //@ts-ignore
                    this.nbpts = geometry.getCoordinates().length;
                    return geometry;
                }
            }),
            // Options bar associated with the control
            bar: new Bar.default({
                controls: [
                    new TextButton.default({
                        className: 'deleteBtn',
                        html: 'undo',
                        title: "Delete last point",
                        handleClick: function () {
                            //@ts-ignore
                            if (ledit.getInteraction().nbpts > 1) ledit.getInteraction().removeLastPoint();
                        }
                    }),
                    new TextButton.default({
                        className: 'finishBtn',
                        html: 'finish',
                        title: "Finish drawing",
                        handleClick: function () {
                            // Prevent null objects on finishDrawing
                            //@ts-ignore
                            if (ledit.getInteraction().nbpts > 2) ledit.getInteraction().finishDrawing();
                        }
                    })
                ]
            })
        });

        var fedit = new Toggle.default({
            html: '<i class="fa fa-bookmark-o fa-rotate-270" ></i>',
            title: 'Draw Polygon Feature',
            //@ts-ignore
            interaction: new Draw({
                type: GeometryType.POLYGON,
                source: vector.getSource(),
                // Count inserted points
                geometryFunction: function (coordinates, geometry) {
                    //@ts-ignore
                    this.nbpts = coordinates[0].length;
                    //@ts-ignore
                    if (geometry) geometry.setCoordinates([coordinates[0].concat([coordinates[0][0]])]);
                    //@ts-ignore
                    else geometry = new Polygon(coordinates);
                    return geometry;
                }
            }),
            // Options bar ssociated with the control
            bar: new Bar.default({
                controls: [new TextButton.default({
                    className: "polyUndo",
                    html: 'undo',//'<i class="fa fa-mail-reply"></i>',
                    title: "Delete last point",
                    handleClick: function () {
                        //@ts-ignore
                        if (fedit.getInteraction().nbpts > 1) fedit.getInteraction().removeLastPoint();
                    }
                }),
                new TextButton.default({
                    className: 'finishPoly',
                    html: 'finish',
                    title: "finish drawing",
                    handleClick: function () {
                        // Prevent null objects on finishDrawing
                        //@ts-ignore
                        if (fedit.getInteraction().nbpts > 3) fedit.getInteraction().finishDrawing();
                    }
                })
                ]
            })
        });
        // // Print control
        // let printControl = new PrintDialog.default({});
        // //@ts-ignore
        // map.addControl(printControl);
        // //@ts-ignore
        // printControl.on('print', function (e: any) {
        //     // Print success
        //     if (e.canvas) {
        //         //@ts-ignore
        //         window.open().document.write('<img src="' + e.canvas.toDataURL() + '"/>');
        //     } else {
        //         console.warn('No canvas to export');
        //     }
        // });
        // Define a new legend
        let legend = new Legend.default({
            title: 'Legende',
            style: selectedStyle,
        })

        let legendCtrl = new LegendControl.default({
            // className: 'legendControl',
            title: 'MapLegend',
            legend: legend,
            collapsed: true
        });

        fillLegend(legend)

        let tooltip = new Tooltip.default({});
        //@ts-ignore
        selectCtrl.getInteraction('Select').on('select', function (e: any) {
            //@ts-ignore
            if (this.getFeatures().getLength()) {
                tooltip.setInfo(`ID: ${e.feature}`);
            }
            else tooltip.setInfo();
        });
        //@ts-ignore
        selectCtrl.getInteraction('Select').on('change:active', function (e: any) {
            tooltip.setInfo(e.oldValue ? '' : 'Select feature on map');
        });
        //@ts-ignore      
        pedit.getInteraction('DrawPoint').on('change:active', function (e: any) {
            tooltip.setInfo(e.oldValue ? '' : 'Click map to place a point...');
        });
        //@ts-ignore
        ledit.getInteraction('DrawLine').on(['change:active', 'drawend'], function (e: any) {
            //@ts-ignore
            tooltip.setFeature();
            tooltip.setInfo(e.oldValue ? '' : 'Click map to start drawing line...');
        });
        //@ts-ignore
        ledit.getInteraction('DrawLine').on('drawstart', function (e: any) {
            tooltip.setFeature(e.feature);
            tooltip.setInfo('Click to continue drawing line...');
        });
        //@ts-ignore
        fedit.getInteraction('DrawPolygon').on('drawstart', function (e: any) {
            tooltip.setFeature(e.feature);
            tooltip.setInfo('Click to continue drawing polygon...');
        });
        //@ts-ignore
        fedit.getInteraction('DrawPolygon').on(['change:active', 'drawend'], function (e: any) {
            //@ts-ignore
            tooltip.setFeature();
            tooltip.setInfo(e.oldValue ? '' : 'Click map to start drawing polygon...');
        });

        var ctrl = new LayerSwitcher.default();
        //@ts-ignore
        map.addControl(ctrl);


        //@ts-ignore
        map.addOverlay(tooltip);
        //@ts-ignore
        map.addControl(legendCtrl);

        editbar.addControl(selectCtrl);
        editbar.addControl(pedit);
        editbar.addControl(ledit);
        editbar.addControl(fedit);
        mainbar.addControl(editbar);
        mainbar.addControl(save);
        //@ts-ignore
        map.addControl(mainbar);

    }

}




