
import React, { useState } from 'react';
import 'ol/ol.css';
import { IMapSidebarProps, TSelectableTabs } from '../types/types';
import Sidebar from './Sidebar';
import Tab from './Tab';
import { Feature, MapBrowserEvent } from 'ol';
import { FeatureLike } from 'ol/Feature';
import { Icons, } from '../utils/MapConfig';
import Property from './Property';
import Image from './Image';


const mapListeners = [];

const MapSidebar = (props: IMapSidebarProps): JSX.Element => {

    const { map } = props;

    const [collapsed, setCollapsed] = useState(false);
    const [selected, setSelected] = useState<TSelectableTabs>('home');
    const [selectedFeature, setSelectedFeature] = useState(new Feature());


    function onClose() {
        setCollapsed(true);
        //@ts-ignore
        setSelected(undefined);
    }
    function onOpen(id: string) {
        setCollapsed(false)
        //@ts-ignore
        setSelected(id)
    };


    let prevSelected: Feature<any>[] = []
    if (map) {
        if (mapListeners.length === 0) {

            const mapListener = map.on('click', function (e: MapBrowserEvent<any>): void {
                map!.forEachFeatureAtPixel(e.pixel, (feature: Feature<any> | FeatureLike): void => {
                    let castedFeature: Feature<any> = feature as Feature<any>;
                    prevSelected.unshift(castedFeature)
                    setSelectedFeature(prevSelected[0]);
                    // setSelected('data')
                    // setCollapsed(false)
                    prevSelected[0].set('selected', true);
                    if (prevSelected.length > 1) {
                        prevSelected[1].set('selected', false);
                    }
                    prevSelected.splice(1)
                });
            });
            mapListeners.push(mapListener);

        }
    }

    const checkFeature = selectedFeature.getKeys()


    // draw.on('drawend', drawEnd);

    return (
        <>
            <Sidebar
                id='sidebar'
                collapsed={collapsed}
                selected={selected}
                onClose={onClose}
                //@ts-ignore
                doOpen={onOpen}
            >
                <Tab
                    id='home'
                    header='Home'
                    faIcon={Icons.Home}
                >
                    {/* <p>TODO: CSV Export, Editieren der Daten...</p> */}
                    <Image link="https://www.nabu-koeln.de/" img="https://upload.wikimedia.org/wikipedia/de/thumb/1/17/Nabu-logo.svg/2000px-Nabu-logo.svg.png" altText="Nabu Kln" />
                </Tab>
                <Tab
                    id='data'
                    header='Datenausgabe'
                    faIcon={Icons.Data}
                >
                    {checkFeature.length === 0 ?
                        'Keine Daten selektiert.'
                        :
                        <>
                            <Property keys={selectedFeature.getKeys()} feature={selectedFeature} map={map} />
                        </>
                    }
                </Tab>
                <Tab
                    id='instructions'
                    header='Anleitung'
                    faIcon={Icons.Tools}
                    anchor='top'
                >
                </Tab>
                <Tab
                    id='info'
                    header='Mehr Infos'
                    faIcon={Icons.Info}
                    anchor='bottom'
                >
                </Tab>
            </Sidebar>
        </>
    )
}

export default MapSidebar;