import React, { useState } from 'react';
import { IPropertyProps } from '../types/types';
import GeometryType from 'ol/geom/GeometryType';
import Button from './Button';
import { Icons } from '../utils/MapConfig';
import { getLength, getArea } from 'ol/sphere';
import LineString from 'ol/geom/LineString';
import { MapBrowserEvent} from 'ol';
import Polygon from 'ol/geom/Polygon';



const Property = (props: IPropertyProps) => {

    const [length, setLength] = useState("");
    const [area, setArea] = useState("")
    const [active, setActive] = useState(length)


    const {
        map,
        keys,
        feature
    } = props


    function getVal(k: number, t: string) {
        if (t !== "geometry" && t !== "selected") {
            return JSON.stringify(feature.get(t))
        }
    }

    function getKey(k: number, t: string) {
        if (t !== "geometry" && t !== "selected") {
            return t
        }
    }

    //@ts-ignore
    let featType: GeometryType = feature!.getGeometry().getType() as GeometryType 
    let line: LineString = feature!.getGeometry() as LineString
    let poly: Polygon = feature!.getGeometry() as Polygon

    function exportPdf(divId: string, title: string) {
        let mywindow = window.open('', 'PRINT', 'height=650,width=900,top=100,left=150');
        let elm = document.getElementById(divId) as HTMLElement;

        mywindow!.document.write(`<html><head><title>${title}</title>`);
        mywindow!.document.write('</head><body >');
        mywindow!.document.write(elm.innerHTML);
        mywindow!.document.write('</body></html>');

        mywindow!.document.close(); // necessary for IE >= 10
        mywindow!.focus(); // necessary for IE >= 10*/

        mywindow!.print();
        mywindow!.close();

        return true;
    }

    const formatLength = (line: LineString): string => {
        const length = getLength(line);
        let output;
        if (length > 1000) {
            output = Math.round((length / 1000) * 100) / 100 + ' km';
        } else {
            output = Math.round(length * 100) / 100 + ' m';
        }
        return output;
    };

    const formatArea = (polygon: Polygon): string => {
        const area = getArea(polygon);
        let output;
        if (area > 100000) {
            output = Math.round((area / 1000000) * 100) / 100 + ' km';
        } else {
            output = Math.round(area * 100) / 100 + ' m';
        }
        return output;
    };

    const pointerMoveHandler = (evt: MapBrowserEvent<any>): void => {
        if (evt.dragging) {
            return;
        }
        if (featType === GeometryType.LINE_STRING) {
            setActive(length)
            setLength(formatLength(line))
        }
        else if (featType === GeometryType.POLYGON){
            setActive(area)
            setArea(formatArea(poly))
        }
        else {
            return 
        }
    }

    map!.on('click', pointerMoveHandler)

    return (
        <>
            {featType !== GeometryType.LINE_STRING && featType !== GeometryType.POLYGON ?
                <>
                    <div id="featureprops" className="featureprops">
                        {keys.map((t, k) => <div id={t} key={k}><b> {getKey(k, t)}</b> <p>{getVal(k, t)}</p></div>)}
                    </div>
                    <Button
                        className='csvButton'
                        buttonText={`Export as CSV `}
                        faIcon={Icons.Csv}
                        clickButton={() => console.log("CSV EXPORT")}
                    />
                    <Button
                        className='pdfButton'
                        buttonText={`Export as PDF `}
                        faIcon={Icons.Pdf}
                        clickButton={() => exportPdf("featureprops", "Feature Properties")}
                    />
                </>
                :
                
                <p>Gemessen: {active}</p>
            }
        </>
    )
}

export default Property;
